.date-range {
  display: flex;
  justify-content: space-between;
  width: 100%;

  &__last-date,
  &__first-date {
    width: 100%;
    max-width: 9.375rem;
    display: block;
  }

  &__first-date-title,
  &__last-date-title {
    margin-bottom: 0.3125rem;
  }
}

.date-filter,
.date-range__label {
  position: relative;
}

.date-filter::after,
.date-range__label::after {
  position: absolute;
  content: " ";
  border: solid $dark-shade-50;
  border-width: 0 0.125rem 0.125rem 0;
  display: inline-block;
  float: right;
  padding: 0.1875rem;
  top: 0.25rem;
  right: 1.125rem;
  transform: rotate(45deg); 
}

.date-filter__input,
.date-range__date-in,
.date-range__date-out {
  position: relative;
  display: inline-block;
  width: 100%;
  min-height: 2.75rem;
  margin: 0rem;
  padding-left: 0.875rem;
  box-sizing: border-box;
  background-color: white;
  cursor: pointer;
  font-family: $mainFont;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.0em;
  @include border-input-default;
}
